.slider {
  box-shadow: 0px 20px 31px -20px rgba(0,0,0,0.66);
  position: relative;
  z-index: 10;
  &__container {
    overflow-x: hidden; }
  .container {
    position: relative; }
  &__block {
    display: flex;
    height: 430px;
    .row {
      height: inherit; }
    &__text {
      position: relative;
      color: white;
      width: 45%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 0 70px;
      font-family: 'Blogger Sans';
      padding-top: 35px;
      &__sale {
        margin-bottom: 10px;
        span {
          color: white;
          font-size: 100px;
          text-transform: uppercase;
          display: block;
          text-align: center; } }
      h2 {
        font-size: 33px;
        margin-bottom: 30px;
        font-weight: normal; }
      &__prices {
        display: flex;
        justify-content: space-between;
        p {
          font-size: 95px;
          line-height: 75px;
          margin-bottom: 15px;
          color: white;
          font-weight: normal; }
        span {
          font-size: 33px;
          display: block;
          text-align: center; }
        .second {
          p {
            font-size: 65px;
            position: relative;
            bottom: -11px;
            color: white;
            font-weight: normal; } } }
      h4 {
        text-align: right;
        color: #1e6381;
        font-size: 24px;
        margin-top: 30px;
        font-weight: normal; } }
    &__image {
      width: 55%;
      img {
        @include object-fit(cover);
        width: 100%;
        height: 100%;
        display: block; } } } }


.owl {
  &-carousel {}
  &-stage {
    height: 430px;
    overflow: hidden; }
  &-dots {
    display: none; }
  &-item {
    background: $primary;
    filter: blur(2px);
    transform: scale(1.1) !important;
    transition: all 0.3s;
    &.active {
      filter: blur(0px);
      transform: scale(1) !important;
      position: relative;
      z-index: 10; } } }

.next, .prev {
    position: absolute;
    z-index: 100;
    cursor: pointer;
    top: 50%;
    transform: translateY(-50%);
    color: $primary3;
    font-size: 50px; }
.next {
  right: -50px; }
.prev {
  left: -40px; }


.owl-carousel .owl-stage-outer {
  overflow: none !important; }




