.rate {
  padding: 25px 0;
  background: $bg;
  .white {
    background: white;
    padding: 30px 20px;
    padding-bottom: 35px; }
  .heading {
    h2 {
      margin-bottom: 25px; } }
  &__block {
    width: 100%;
    border: 1px solid #d4d4d4;
    padding: 20px 17px;
    overflow: hidden;
    position: relative;
    margin-bottom: 35px;
    &__sale {
      width: 150px;
      height: 30px;
      background-color: #fec007;
      position: absolute;
      top: 21px;
      right: -34px;
      display: flex;
      align-items: center;
      justify-content: center;
      transform: rotate(44deg);
      span {
        color: $grey;
        font-size: 14px;
        text-transform: uppercase; } }
    &__rate, &__traffic, &__speed, &__price {
      border-bottom: 1px solid #cdcece;
      display: flex;
      flex-direction: column;
      justify-content: center;
      position: relative; }
    &__price {
      border-bottom: none; }
    &__rate {
      height: 110px;
      h4 {
        top: 0 !important; } }
    &__traffic {
      height: 100px;
      i {
        color: #fec007;
        font-size: 55px;
        width: 100%;
        display: flex;
        justify-content: center;
        position: relative;
        top: 10px; } }
    &__speed {
      padding-top: 22px;
      height: 105px; }
    &__price {
      padding-top: 20px;
      height: 115px; }
    h4 {
      font-size: 14px;
      font-weight: normal;
      color: $grey;
      position: absolute;
      top: 10px; }
    p {
      color: $primary;
      font-size: 21px;
      text-align: center;
      font-family: 'Blogger Sans'; }
    span {
      display: block;
      font-size: 14px;
      text-align: center;
      color: black;
      font-family: 'Lato'; }
    hr {
      border: none;
      border-bottom: 1px solid #cdcece; }
    .btn {
      position: relative;
      left: 50%;
      transform: translateX(-50%); } }
  .many {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    h4 {
      width: 100%; }
    p {
      position: relative;
      width: 33%;
      text-align: center; }
    hr {
      border-left: 1px solid #cdcece;
      height: 100%;
      position: relative;
      bottom: 10px; } }
  &__more {
    display: flex;
    justify-content: center; } }
