form input::placeholder { color: #646464; }

form input[type="submit"] { width: 230px; height: 43px; display: flex; align-items: center; justify-content: center; font-size: 18px; text-decoration: none; border-radius: 3px; transition: all 0.3s; background: #1aafef; border: 2px solid #1aafef; color: #fff; }

.slider { box-shadow: 0px 20px 31px -20px rgba(0, 0, 0, 0.66); position: relative; z-index: 10; }

.slider__container { overflow-x: hidden; }

.slider .container { position: relative; }

.slider__block { display: flex; height: 430px; }

.slider__block .row { height: inherit; }

.slider__block__text { position: relative; color: white; width: 45%; display: flex; flex-direction: column; justify-content: center; padding: 0 70px; font-family: 'Blogger Sans'; padding-top: 35px; }

.slider__block__text__sale { margin-bottom: 10px; }

.slider__block__text__sale span { color: white; font-size: 100px; text-transform: uppercase; display: block; text-align: center; }

.slider__block__text h2 { font-size: 33px; margin-bottom: 30px; font-weight: normal; }

.slider__block__text__prices { display: flex; justify-content: space-between; }

.slider__block__text__prices p { font-size: 95px; line-height: 75px; margin-bottom: 15px; color: white; font-weight: normal; }

.slider__block__text__prices span { font-size: 33px; display: block; text-align: center; }

.slider__block__text__prices .second p { font-size: 65px; position: relative; bottom: -11px; color: white; font-weight: normal; }

.slider__block__text h4 { text-align: right; color: #1e6381; font-size: 24px; margin-top: 30px; font-weight: normal; }

.slider__block__image { width: 55%; }

.slider__block__image img { -o-object-fit: cover; object-fit: cover; font-family: "object-fit: cover"; width: 100%; height: 100%; display: block; }

.owl-stage { height: 430px; overflow: hidden; }

.owl-dots { display: none; }

.owl-item { background: #1aafef; filter: blur(2px); transform: scale(1.1) !important; transition: all 0.3s; }

.owl-item.active { filter: blur(0px); transform: scale(1) !important; position: relative; z-index: 10; }

.next, .prev { position: absolute; z-index: 100; cursor: pointer; top: 50%; transform: translateY(-50%); color: #1b749b; font-size: 50px; }

.next { right: -50px; }

.prev { left: -40px; }

.owl-carousel .owl-stage-outer { overflow: none !important; }

.rate { padding: 25px 0; background: #ebebeb; }

.rate .white { background: white; padding: 30px 20px; padding-bottom: 35px; }

.rate .heading h2 { margin-bottom: 25px; }

.rate__block { width: 100%; border: 1px solid #d4d4d4; padding: 20px 17px; overflow: hidden; position: relative; margin-bottom: 35px; }

.rate__block__sale { width: 150px; height: 30px; background-color: #fec007; position: absolute; top: 21px; right: -34px; display: flex; align-items: center; justify-content: center; transform: rotate(44deg); }

.rate__block__sale span { color: #464646; font-size: 14px; text-transform: uppercase; }

.rate__block__rate, .rate__block__traffic, .rate__block__speed, .rate__block__price { border-bottom: 1px solid #cdcece; display: flex; flex-direction: column; justify-content: center; position: relative; }

.rate__block__price { border-bottom: none; }

.rate__block__rate { height: 110px; }

.rate__block__rate h4 { top: 0 !important; }

.rate__block__traffic { height: 100px; }

.rate__block__traffic i { color: #fec007; font-size: 55px; width: 100%; display: flex; justify-content: center; position: relative; top: 10px; }

.rate__block__speed { padding-top: 22px; height: 105px; }

.rate__block__price { padding-top: 20px; height: 115px; }

.rate__block h4 { font-size: 14px; font-weight: normal; color: #464646; position: absolute; top: 10px; }

.rate__block p { color: #1aafef; font-size: 21px; text-align: center; font-family: 'Blogger Sans'; }

.rate__block span { display: block; font-size: 14px; text-align: center; color: black; font-family: 'Lato'; }

.rate__block hr { border: none; border-bottom: 1px solid #cdcece; }

.rate__block .btn { position: relative; left: 50%; transform: translateX(-50%); }

.rate .many { display: flex; justify-content: space-between; flex-wrap: wrap; }

.rate .many h4 { width: 100%; }

.rate .many p { position: relative; width: 33%; text-align: center; }

.rate .many hr { border-left: 1px solid #cdcece; height: 100%; position: relative; bottom: 10px; }

.rate__more { display: flex; justify-content: center; }

.address { padding: 20px 0; background: #ebebeb; }

.address .container { background: white; padding-top: 45px; padding-bottom: 55px; }

.address h2 { font-size: 30px; font-weight: 300; color: #464646; text-align: center; margin-bottom: 20px; }

.address__form { display: flex; justify-content: space-between; width: 70%; }

.address__form input { border-radius: 3px; border: 1px solid #d4d4d4; font-size: 16px; color: #646464; padding: 15px; max-height: 43px; font-weight: 300; }

.address__form input::placeholder { color: #646464; }

.news { padding: 15px 0 30px; background: #ebebeb; }

.news .heading h2 { margin-bottom: 20px; padding-left: 20px; }

.news .row { justify-content: space-between; }

.news__block { width: 32.5%; background: white; padding: 15px 17px; border-bottom: 2px solid #c4c4c4; }

.news article { padding-bottom: 20px; }

.news article header img { -o-object-fit: cover; object-fit: cover; font-family: "object-fit: cover"; width: 100%; height: 215px; margin-bottom: 15px; }

.news article header h1 { font-size: 16px; color: #323232; margin-bottom: 40px; }

.news article main p { font-size: 13px; margin-bottom: 25px; }

.news article footer { display: flex; justify-content: space-between; }

.news article footer span { font-size: 14px; font-weight: 300; color: #828282; }

.news article footer a { font-size: 14px; color: #1aafef; text-decoration: none; }

.contact .heading { padding: 17px 0; padding-left: 75px; }

.contact__block { background: white; padding: 50px 60px 0px 0px; }

.contact__block__image { width: 90%; height: 100%; display: flex; align-items: flex-end; }

.contact__block__image img { width: 100%; }

.contact__block__phone { margin-bottom: 15px; }

.contact__block__phone a { color: #009fe3; font-size: 30px; font-weight: bold; display: block; text-decoration: none; margin-bottom: 10px; }

.contact__block__phone a span { color: #464646; font-size: 18px; font-weight: 300; margin-left: 15px; }

.contact__block__email a { display: flex; text-decoration: none; align-items: flex-end; margin-bottom: 10px; }

.contact__block__email a i { font-size: 40px; color: #009fe3; margin-right: 15px; }

.contact__block__email a p { font-size: 24px; color: #009fe3; font-weight: 600; margin-right: 15px; }

.contact__block__email a span { font-size: 24px; color: #464646; font-weight: 300; }

.contact__block__form h2 { font-size: 24px; font-weight: 400; color: #009fe3; display: flex; align-items: center; margin-bottom: 10px; }

.contact__block__form h2 i { font-size: 43px; margin-right: 15px; }

.contact__block__form form { margin-left: 60px; }

.contact__block__text { width: 105%; margin-top: 20px; margin-bottom: 35px; margin-left: -50px; }

.contact__block__text h3 { font-size: 30px; font-weight: 300; }

.contact__block__text h3 p { text-align: right; }

.contact__block__text h3 p span { font-size: 50px; font-weight: 600; }

.connect .heading { padding: 17px 0; padding-left: 75px; }

.connect__check { background: White; padding: 30px 80px 0; display: flex; justify-content: center; flex-wrap: wrap; margin-bottom: 35px; }

.connect__check h2 { font-size: 22px; color: #464646; text-align: center; font-weight: normal; margin-bottom: 45px; width: 100%; }

.connect__check form { flex-wrap: wrap; width: 58%; margin-bottom: 30px; }

.connect__check__input { display: flex; width: 100%; justify-content: space-between; margin-bottom: 40px; }

.connect__check__input__street { width: 350px; }

.connect__check__input__home { width: 100px; }

.connect__check__button { width: 100%; display: flex; justify-content: center; }

.connect__check p { margin-bottom: 36px; text-align: left; width: 100%; }

.connect__check p img { width: 150px; margin: 0 15px; }

.connect__check p a { color: #009fe3; text-decoration: none; }

.connect__text { padding: 50px 50px 15px; background-color: white; margin-bottom: 2px; background-image: url("../img/connect/bg-1.png"); background-repeat: no-repeat; background-size: cover; background-position: 100% 100%; }

.connect__text h2 { font-size: 90px; color: #009fe3; font-weight: normal; padding-left: 90px; margin-bottom: 20px; }

.connect__text h3 { font-size: 60px; font-weight: 300; color: #464646; padding-left: 60px; }

.connect__text h4 { margin-top: -20px; font-size: 140px; color: #009fe3; font-weight: bold; padding-left: 160px; }

.connect__text h4 span { font-size: 76px; font-weight: 500; }

.connect__text p { font-size: 20px; }

.connect__content { background: white; padding: 40px 75px; margin-bottom: 35px; }

.connect__content h3 { font-size: 22px; line-height: 1.36; color: #009fe3; font-weight: normal; margin-bottom: 27px; }

.connect__content p { margin-bottom: 23px; }

.connect__content ul { margin-bottom: 33px; }

.connect__content ul p { margin-bottom: 5px; }

.connect__content ul li { font-size: 22px; font-weight: 300; line-height: 1.45; color: #464646; padding-left: 20px; position: relative; }

.connect__content ul li:before { content: ''; width: 10px; height: 10px; border-radius: 50%; background: #009fe3; display: block; position: absolute; top: 13px; left: 0; }

.connect__abonent { background: white; position: relative; padding: 55px 30px 0; display: flex; flex-wrap: wrap; height: 430px; }

.connect__abonent__img { width: 35%; position: relative; }

.connect__abonent__img img { position: absolute; bottom: 0px; }

.connect__abonent__text { width: 65%; }

.connect__abonent__text p img { width: 150px; margin-left: 15px; }

.connect__abonent__text h3 { font-size: 36px; color: #009fe3; font-weight: normal; margin-top: 55px; }

.connect__abonent__text h3 p { font-size: 36px; color: #009fe3; font-weight: normal; position: relative; margin-bottom: 35px; left: 170px; top: -15px; }

.connect__abonent__text h3 p span { font-size: 55px; }
